import classes from './film.module.css'

import TimeTable from './../timetable/timetable'
import { BrowserRouter, Router, Route, Redirect } from "react-router-dom";

let FilmPage = (props) => {
    console.log(props.data)
    return (
        <div>
            
            <div className={classes.FilmPage}>
                <h1 className={classes.FilmTitle}>{props.data[0].title}</h1>
                <h3>Трейлер:</h3>
                <div className={classes.tralier}>{props.data[0].tralier}</div>
                <div className={classes.information__film}>{props.data[0].infromation}</div>
                <div className={classes.wrapper__buy}><a href={props.data[0].link} >Купить билеты</a></div>
                <TimeTable data=  {props.state.timetablePage} />
            </div>
        </div>




    )
}


export default FilmPage