import Item from './item/item'
import classes from './timetable.module.css'


let TimeTable = (props) => {
    console.log(props)
    let NewItem = props.data.map(data => <Item title={data.title} place={data.place} time={data.time} />)
    return (
        <div >
            <div className={classes.title}> <h1>Расписание фильмов:</h1></div>


            <div className={classes.wrapper}>
                {NewItem}
            </div>
        </div>
    )
}



export default TimeTable